export default "var editor = monaco.editor.create(document.getElementById(\"container\"), {\n\tvalue: [\n\t\t\"\",\n\t\t\"class Example {\",\n\t\t\"\\tprivate m:number;\",\n\t\t\"\",\n\t\t\"\\tpublic met(): string {\",\n\t\t'\\t\\treturn \"Hello world!\";',\n\t\t\"\\t}\",\n\t\t\"}\",\n\t].join(\"\\n\"),\n\tlanguage: \"typescript\",\n});\n\n// Explanation:\n// Press F1 => the action will appear and run if it is enabled\n// Press Ctrl-F10 => the action will run if it is enabled\n// Press Chord Ctrl-K, Ctrl-M => the action will run if it is enabled\n\neditor.addAction({\n\t// An unique identifier of the contributed action.\n\tid: \"my-unique-id\",\n\n\t// A label of the action that will be presented to the user.\n\tlabel: \"My Label!!!\",\n\n\t// An optional array of keybindings for the action.\n\tkeybindings: [\n\t\tmonaco.KeyMod.CtrlCmd | monaco.KeyCode.F10,\n\t\t// chord\n\t\tmonaco.KeyMod.chord(\n\t\t\tmonaco.KeyMod.CtrlCmd | monaco.KeyCode.KeyK,\n\t\t\tmonaco.KeyMod.CtrlCmd | monaco.KeyCode.KeyM\n\t\t),\n\t],\n\n\t// A precondition for this action.\n\tprecondition: null,\n\n\t// A rule to evaluate on top of the precondition in order to dispatch the keybindings.\n\tkeybindingContext: null,\n\n\tcontextMenuGroupId: \"navigation\",\n\n\tcontextMenuOrder: 1.5,\n\n\t// Method that will be executed when the action is triggered.\n\t// @param editor The editor instance is passed in as a convenience\n\trun: function (ed) {\n\t\talert(\"i'm running => \" + ed.getPosition());\n\t},\n});\n";